import {Component, Mapper, Filter} from '../../dist/graflow'

const SimpleCycle = () => Component({
  components: {
    inc: Mapper(v => v + 1),
    limit: Filter(v => v <= 5)
  },
  connections: [
    ['in', 'inc'],
    ['inc', 'limit'],
    ['limit', 'out'],
    ['limit', 'inc']
  ]
})

export default SimpleCycle



